import { KOLOR_PARTII } from "./dane";
import { Histogram, PoparciePartii, Sondaze } from "./typy";
import Chart, { ChartItem } from "chart.js/auto";
import Color from 'color';

export function rysujWynikWyborow(wynikWyborow: Sondaze, targetElement: string, options: { poziomo?: boolean, laczOpozycje?: boolean } = {}) {
    new Chart(document.getElementById(targetElement) as ChartItem, {
      type: "bar",
      data: {
        labels: Object.entries(wynikWyborow).map(([sondaz, wynik]) =>
          sondaz.toString()
        ),
        datasets: !options.laczOpozycje
          ? [
              {
                label: "Lewica",
                data: Object.values(wynikWyborow).map((wynik) => wynik.LEWICA),
                backgroundColor: KOLOR_PARTII['LEWICA'],
              },
              {
                label: "KO",
                data: Object.values(wynikWyborow).map((wynik) => wynik.KO),
                backgroundColor: KOLOR_PARTII['KO'],
              },
              {
                label: "Trzecia Droga",
                data: Object.values(wynikWyborow).map((wynik) => wynik.TD),
                backgroundColor: KOLOR_PARTII['TD'],
              },
              {
                label: "Konfederacja",
                data: Object.values(wynikWyborow).map((wynik) => wynik.KONF),
                backgroundColor: KOLOR_PARTII['KONF'],
              },
              {
                label: "PiS",
                data: Object.values(wynikWyborow).map((wynik) => wynik.PIS),
                backgroundColor: KOLOR_PARTII['PIS'],
              },
            ]
          : [
              {
                label: "Opozycja Demokratyczna",
                data: Object.values(wynikWyborow).map((wynik) => wynik.DEMO),
                backgroundColor: "#E56701",
              },
              {
                label: "PiS i Konfederacja",
                data: Object.values(wynikWyborow).map((wynik) => wynik.NIEDEMO),
                backgroundColor: "rgb(38, 55, 120)",
              },
            ],
      },
      options: options?.poziomo
        ? {
            indexAxis: "y",
            scales: {
              x: {
                stacked: true,
              },
              y: {
                stacked: true,
              },
            },
          }
        : {},
    });
}

export function rysujHistogram(
  histogram: PoparciePartii[],
  targetElement: string,
  color: string = "rgb(150, 195, 110)",
  nazwa: string
) {
  const tooltip = (items) => { 
    // console.log(items)
  //   // return histogram.map(
  //   //     ({ procent, czestosc, mandaty }) =>
  //   //       `${mandaty || 0} mandat${
  //   //         mandaty > 0 ? (mandaty > 1 ? "y" : "") : "w"
  //   //       }`
  //   //   ),
  };
  const mandatKolor = (color)
  new Chart(document.getElementById(targetElement) as ChartItem, {
    type: "bar",
    data: {
      // labels: histogram.map(({ procent, czestosc }) => procent),
      labels: histogram.map(({ procent, czestosc, mandaty }) => `${procent}% -> ${mandaty}`),
      // labels: histogram.map(
      //   ({ procent, czestosc, mandaty }) =>
      //     `${mandaty || 0} mandat${
      //       mandaty > 0 ? (mandaty > 1 ? "y" : "") : "w"
      //     }`
      // ),
      datasets: [
        {
          label: nazwa,
          data: histogram.map(({ procent, czestosc }) => czestosc),
          backgroundColor: histogram.map(({ procent, czestosc, mandaty }) => {
            const col = Color(color)
              .fade(["PIS", "KO"].includes(nazwa.split(":")[0]) ? 0.1 : 0.5)
              .opaquer(mandaty / 5)
              .toString();
            // console.log(color, col, (mandaty || 0) / 3);
            return col;
          }),
        },
      ],
    },
    options: {
      scales: {
        x: {
          // display: false,
        },
        y: {
          display: false,
        },
      },
      tooltip: {
        callbacks: {
          label: tooltip,
        },
      },
    },
  });
}

